
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'SvgIcon',
  props: {
    className: {
      type: String,
      default: '',
    },
    name: {
      type: String,
      required: true,
      default: '',
    },
  },
  setup(props) {
    return { props }
  },
})
